import { FC, ChangeEvent, useState } from 'react'
import PropTypes from 'prop-types'
import {
  Divider,
  Box,
  Button,
  FormControl,
  InputLabel,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Select,
  MenuItem,
  Typography,
  CardHeader,
  Checkbox,
  useTheme,
  useMediaQuery
} from '@mui/material'

import Label from 'src/components/Label'
import { Member, MemberStatus } from 'src/models/member'
import { MEMBER_VALUES } from 'src/data/MemberValues'
import StarIcon from '@mui/icons-material/Star'
import SupervisorAccountIcon from '@mui/icons-material/SupervisorAccount'
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted'
import ViewCompactIcon from '@mui/icons-material/ViewCompact'
import BulkActions from './BulkActions'

interface MembersTableProps {
  className?: string
  members: Member[]
  selectedMembers: Member[]
  setSelectedMembers: any
  handleEdit(): void
  handleFire(): void
}

interface Filters {
  status?: MemberStatus
}

const getStatus = (member: Member): MemberStatus => {
  if (member.deletedAt) return 'DELETED'
  if (member.firedAt) return 'FIRED'
  if (member.isPaused) return 'PAUSED'

  return 'ACTIVE'
}

const getStatusLabel = (memberStatus: MemberStatus): JSX.Element => {
  const map = {
    ACTIVE: {
      text: MEMBER_VALUES.status[0].display,
      color: 'success'
    },
    PAUSED: {
      text: MEMBER_VALUES.status[1].display,
      color: 'warning'
    },
    FIRED: {
      text: MEMBER_VALUES.status[2].display,
      color: 'error'
    },
    DELETED: {
      text: MEMBER_VALUES.status[3].display,
      color: 'error'
    }
  }

  const { text, color }: any = map[memberStatus]

  return (
    <Label color={color}>
      <Typography textAlign="center" noWrap>
        {text}
      </Typography>
    </Label>
  )
}

const applyFilters = (members: Member[], filters: Filters): Member[] => {
  return members.filter(member => {
    let matches = true

    if (filters.status && getStatus(member) !== filters.status) {
      matches = false
    }

    return matches
  })
}

const applyPagination = (members: Member[], page: number, limit: number): Member[] => {
  return members.slice(page * limit, page * limit + limit)
}

const MembersTable: FC<MembersTableProps> = ({
  members,
  handleEdit,
  selectedMembers,
  setSelectedMembers,
  handleFire
}) => {
  const selectedBulkActions = selectedMembers.length > 0

  const [page, setPage] = useState<number>(0)
  const [limit, setLimit] = useState<number>(5)
  const [filters, setFilters] = useState<Filters>({
    status: null
  })
  const [viewGrid, setViewGrid] = useState<boolean>(true)

  const statusOptions = [
    {
      id: 'all',
      name: 'All'
    },
    {
      id: MEMBER_VALUES.status[0].value,
      name: MEMBER_VALUES.status[0].display
    },
    {
      id: MEMBER_VALUES.status[1].value,
      name: MEMBER_VALUES.status[1].display
    },
    {
      id: MEMBER_VALUES.status[2].value,
      name: MEMBER_VALUES.status[2].display
    },
    {
      id: MEMBER_VALUES.status[3].value,
      name: MEMBER_VALUES.status[3].display
    }
  ]

  const handleSelectAllMembers = (event: ChangeEvent<HTMLInputElement>): void => {
    setSelectedMembers(event.target.checked ? members : [])
  }

  const handleSelectOneMember = (event: ChangeEvent<HTMLInputElement>, member: Member): void => {
    if (!selectedMembers.includes(member)) {
      setSelectedMembers(prevSelected => [...prevSelected, member])
    } else {
      setSelectedMembers(prevSelected => prevSelected.filter(m => m.id !== member.id))
    }
  }

  const handleStatusChange = (e: ChangeEvent<HTMLInputElement>): void => {
    let value = null

    if (e.target.value !== 'all') {
      value = e.target.value
    }

    setFilters(prevFilters => ({
      ...prevFilters,
      status: value
    }))
  }

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage)
  }

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value))
  }

  const filteredMembers = applyFilters(members, filters)
  const paginatedMembers = applyPagination(filteredMembers, page, limit)
  const selectedSomeMembers = selectedMembers.length > 0 && selectedMembers.length < members.length
  const selectedAllMembers = selectedMembers.length === members.length
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  return (
    <Card>
      <Box flex={1} p={1}>
        {selectedBulkActions ? (
          <BulkActions handleFire={handleFire} handleEdit={handleEdit} />
        ) : (
          <CardHeader
            action={
              <Box width={150}>
                <FormControl fullWidth variant="outlined">
                  <InputLabel>활동</InputLabel>
                  {selectedBulkActions && <BulkActions handleFire={handleFire} handleEdit={handleEdit} />}
                  <Select value={filters.status || 'all'} onChange={handleStatusChange} label="Status" autoWidth>
                    {statusOptions.map(statusOption => (
                      <MenuItem key={statusOption.id} value={statusOption.id}>
                        {statusOption.name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Box>
            }
            title={
              matches ? (
                '회원 명단'
              ) : (
                <Button
                  sx={{ ml: 1 }}
                  variant="contained"
                  onClick={() => setViewGrid(!viewGrid)}
                  style={{
                    backgroundColor: theme.colors.secondary.lighter,
                    color: theme.colors.secondary.light,
                    margin: 'none'
                  }}
                >
                  {viewGrid ? <ViewCompactIcon fontSize="small" /> : <FormatListBulletedIcon fontSize="small" />}
                </Button>
              )
            }
          />
        )}
      </Box>

      <Divider />
      <TableContainer>
        <Table>
          {(matches || !viewGrid) && (
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    color="primary"
                    checked={selectedAllMembers}
                    indeterminate={selectedSomeMembers}
                    onChange={handleSelectAllMembers}
                  />
                </TableCell>
                <TableCell>이름</TableCell>
                <TableCell>학과</TableCell>
                <TableCell>학번</TableCell>
                <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                  회원 등급
                </TableCell>
                <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                  활동
                </TableCell>
                <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                  회비
                </TableCell>
                <TableCell align="right" sx={{ whiteSpace: 'nowrap' }}>
                  출석
                </TableCell>
                <TableCell align="center" sx={{ whiteSpace: 'nowrap' }}>
                  비고
                </TableCell>
              </TableRow>
            </TableHead>
          )}
          <TableBody>
            {paginatedMembers.map(member => {
              const isMemberSelected = selectedMembers.filter(m => m.id === member.id).length > 0

              return (
                <TableRow hover key={member.id}>
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isMemberSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) => handleSelectOneMember(event, member)}
                      value={isMemberSelected}
                    />
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      <Box style={{ display: 'flex', alignItems: 'center' }}>
                        {member.name}
                        {member.permission === 'SUPERADMIN' && (
                          <StarIcon htmlColor="orange" sx={{ height: '20px', width: '20px' }} />
                        )}
                        {member.permission === 'ADMIN' && (
                          <SupervisorAccountIcon color="primary" sx={{ height: '20px', width: '20px' }} />
                        )}
                      </Box>
                    </Typography>
                    <Typography variant="caption" color="text.secondary" noWrap>
                      {matches || !viewGrid
                        ? new Date(member.createdAt).toLocaleDateString()
                        : member.department + ' / ' + member.studentId}
                    </Typography>
                  </TableCell>
                  {(matches || !viewGrid) && (
                    <>
                      <TableCell>
                        <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                          {member.department}
                        </Typography>
                      </TableCell>
                      <TableCell>
                        <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                          {member.studentId}
                        </Typography>
                      </TableCell>
                    </>
                  )}
                  <TableCell align="right">
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.level}
                    </Typography>
                  </TableCell>

                  {(matches || !viewGrid) && (
                    <>
                      <TableCell align="right">{getStatusLabel(getStatus(member))}</TableCell>

                      <TableCell align="right">
                        <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                          {member.fee ? 'O' : 'X'}
                        </Typography>
                      </TableCell>
                      <TableCell align="right">
                        <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                          {`${member.attendance} / ${member.totalAttendance}`}
                        </Typography>
                      </TableCell>
                      <TableCell align="center" sx={{ maxWidth: { xs: '100px', md: '12vw' } }}>
                        <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                          {member.note ? member.note : '-'}
                        </Typography>
                      </TableCell>
                    </>
                  )}
                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredMembers.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
          sx={
            !matches && {
              '.MuiToolbar-gutters': {
                width: '100%',
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center'
              }
            }
          }
        />
      </Box>
    </Card>
  )
}

MembersTable.propTypes = {
  members: PropTypes.array.isRequired
}

MembersTable.defaultProps = {
  members: []
}

export default MembersTable
