import {
  Modal,
  Box,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  styled,
  TextareaAutosize
} from '@mui/material'
import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import { MEMBER_VALUES } from 'src/data/MemberValues'
import { Member, Permission } from 'src/models/member'
import EmailIcon from '@mui/icons-material/Email'
import PhoneIcon from '@mui/icons-material/Phone'
import StarIcon from '@mui/icons-material/Star'
import SupervisorAccountIcon from '@mui/icons-material/SupervisorAccount'
import { useAuthUser } from 'react-auth-kit'
import { useParams } from 'react-router'
import { Dispatch, SetStateAction } from 'react'

const modalStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '80%',
  maxWidth: '400px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

interface EditDialogProps {
  open: boolean
  onClose(): void
  handleSubmit(): void
  selectedMembers: Member[]
  setSelectedMembers: Dispatch<SetStateAction<Member[]>>
}

const EditDialog = ({ open, onClose, handleSubmit, selectedMembers, setSelectedMembers }: EditDialogProps) => {
  const member: Member = selectedMembers.length === 1 ? selectedMembers[0] : null

  const authUser = useAuthUser()
  const clubId = parseInt(useParams().id)
  const club = authUser()?.clubs.filter(c => c.id === clubId)[0]

  return (
    <Modal
      open={open}
      onClose={onClose}
      aria-labelledby="회원 정보 수정"
      aria-describedby="수정 후 완료 버튼을 눌러주세요."
    >
      <Box sx={modalStyle}>
        {member ? (
          <>
            <Typography variant="h3" fontWeight="bold" color="text.primary" display="inline" gutterBottom noWrap>
              {member.permission === 'SUPERADMIN' && (
                <StarIcon htmlColor="orange" sx={{ height: '20px', width: '20px' }} />
              )}
              {member.permission === 'ADMIN' && (
                <SupervisorAccountIcon color="primary" sx={{ height: '20px', width: '20px' }} />
              )}
              {member.name}
            </Typography>
            <Typography variant="body2" display="inline" color="text.secondary" noWrap>
              {`(${new Date(member.createdAt).toLocaleDateString()})`}
            </Typography>
            <Typography variant="body1" fontWeight="bold" color="text.secondary" noWrap>
              {`${member.studentId} ${member.department}`}
            </Typography>
            <Grid container mt={1}>
              <Grid item xs={12} md={6}>
                <Typography variant="body1" color="text.secondary" display="flex" alignItems="center">
                  <PhoneIcon sx={{ height: '1rem', width: '1rem', marginRight: '3px' }} />
                  {member.phoneNumber ? `${member.phoneNumber}` : '-'}
                </Typography>
                <Typography variant="body1" color="text.secondary" display="flex" alignItems="center">
                  <EmailIcon sx={{ height: '1rem', width: '1rem', marginRight: '3px' }} />
                  {`${member.email}`}
                </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
                <Typography variant="caption" color="text.secondary" display="block">
                  {`가입 : ${new Date(member.createdAt).toLocaleDateString()}`}
                </Typography>
                {member.deletedAt && (
                  <Typography variant="caption" color="text.secondary" display="block">
                    {`탈퇴 : ${new Date(member.deletedAt).toLocaleDateString()}`}
                  </Typography>
                )}
                {member.firedAt && (
                  <Typography variant="caption" color="text.secondary" display="block">
                    {`제적 : ${new Date(member.firedAt).toLocaleDateString()}`}
                  </Typography>
                )}
              </Grid>
            </Grid>{' '}
          </>
        ) : (
          selectedMembers &&
          selectedMembers[0] && (
            <Box>
              <Typography variant="h3" fontWeight="bold" gutterBottom>
                {`${selectedMembers[0].name} 등 ${selectedMembers.length}명`}
              </Typography>
              <Typography variant="body1" color="text.secondary">
                동시 수정 모드
              </Typography>
            </Box>
          )
        )}

        <FormControl fullWidth sx={{ marginY: 2 }}>
          <InputLabel id="levelLabel">등급</InputLabel>
          <Select
            labelId="levelLabel"
            label="등급"
            id="level"
            defaultValue={member ? member.level : '정회원'}
            onChange={e => {
              setSelectedMembers(
                selectedMembers.map((member: Member) => {
                  return { ...member, level: e.target.value }
                })
              )
            }}
          >
            {MEMBER_VALUES.level.map(l => {
              return (
                <MenuItem key={l.value} value={l.value}>
                  {l.display}
                </MenuItem>
              )
            })}
          </Select>
        </FormControl>

        <FormControl fullWidth sx={{ mb: 2 }}>
          <InputLabel id="feeLabel">회비</InputLabel>
          <Select
            labelId="feeLabel"
            label="회비"
            id="fee"
            defaultValue={member && member.fee ? MEMBER_VALUES.fee[0].value : MEMBER_VALUES.fee[1].value}
            onChange={e => {
              setSelectedMembers(
                selectedMembers.map((member: Member) => {
                  return { ...member, fee: e.target.value === 'O' }
                })
              )
            }}
          >
            {MEMBER_VALUES.fee.map(fee => {
              return <MenuItem value={fee.value}>{fee.display}</MenuItem>
            })}
          </Select>
        </FormControl>

        <FormControl fullWidth sx={{ mb: 2 }}>
          <InputLabel id="isPausedLabel">휴회원 여부</InputLabel>
          <Select
            labelId="isPausedLabel"
            label="휴회원 여부"
            id="isPaused"
            defaultValue={member && member.isPaused ? MEMBER_VALUES.isPaused[0].value : MEMBER_VALUES.isPaused[1].value}
            onChange={e => {
              setSelectedMembers(
                selectedMembers.map((member: Member) => {
                  return { ...member, isPaused: e.target.value === 'O' }
                })
              )
            }}
          >
            {MEMBER_VALUES.isPaused.map(item => {
              return <MenuItem value={item.value}>{item.display}</MenuItem>
            })}
          </Select>
        </FormControl>

        {/* Only SUPERADMIN with one selected member can edit level */}
        {club.level === 'SUPERADMIN' && selectedMembers.length === 1 && (
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel id="permissionLabel">운영진</InputLabel>
            <Select
              labelId="permissionLabel"
              label="운영진"
              id="permission"
              defaultValue={member ? member.permission : 'USER'}
              onChange={e => {
                setSelectedMembers(
                  selectedMembers.map((member: Member) => {
                    return { ...member, permission: e.target.value as Permission }
                  })
                )
              }}
            >
              {MEMBER_VALUES.permission.map(permission => {
                return <MenuItem value={permission.value}>{permission.display}</MenuItem>
              })}
            </Select>
          </FormControl>
        )}

        <FormControl fullWidth>
          <CustomTextareaAutosize
            maxRows={4}
            minRows={3}
            defaultValue={member ? member.note : ''}
            placeholder="비고(영문기준 최대 50자)"
            onChange={e => {
              setSelectedMembers(
                selectedMembers.map((member: Member) => {
                  return { ...member, note: e.target.value }
                })
              )
            }}
          />
        </FormControl>

        <CancelSubmitBtns handleSubmit={handleSubmit} handleCancel={onClose} submitBtnText="수정 완료" />
      </Box>
    </Modal>
  )
}

export default EditDialog
