import { Card } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Member } from 'src/models/member'
import MembersTable from './MembersTable'
import dayjs from 'dayjs'
import FireDialog from './FireDialog'
import { useAuthHeader } from 'react-auth-kit'
import EditDialog from './EditDialog'
import { useSnackbar } from 'notistack'

function MemberList() {
  const [memberList, setMemberList] = useState<Member[]>([])
  const [selectedMembers, setSelectedMembers] = useState<Member[]>([])
  const [isModal, setIsModal] = useState<boolean>(false)
  const [isFireDialog, setIsFireDialog] = useState<boolean>(false)
  const [stateForGetMembers, setStateForGetMembers] = useState<boolean>(false)

  const params = useParams()
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()

  const handleEdit = (): void => {
    setIsModal(true)
  }

  const handleSubmit = async () => {
    try {
      const membersForSubmit = selectedMembers.map(member => {
        return { userId: member.id, clubId: parseInt(params.id), ...member }
      })
      const result = await axios.patch(`${[process.env.REACT_APP_APIHOST]}/club/member`, membersForSubmit, {
        headers: { Authorization: authHeader() }
      })

      if (result.data?.message !== 'ok') throw new Error()

      enqueueSnackbar('성공적으로 수정되었습니다.', { variant: 'success' })
    } catch (e) {
      enqueueSnackbar(e.response?.data?.message, { variant: 'error' })
    }

    setStateForGetMembers(!stateForGetMembers)
    setSelectedMembers([])
    setIsModal(false)
  }

  const handleFire = (): void => {
    setSelectedMembers(
      selectedMembers.map(member => {
        return { ...member, firedAt: dayjs(new Date()).toISOString() }
      })
    )

    setIsFireDialog(true)
  }

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/member?clubId=${params.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<Member[]>) => {
        setMemberList(res.data)
      })
  }, [stateForGetMembers])

  return (
    <Card>
      <MembersTable
        members={memberList}
        handleEdit={handleEdit}
        handleFire={handleFire}
        selectedMembers={selectedMembers}
        setSelectedMembers={setSelectedMembers}
      />
      {selectedMembers && (
        <EditDialog
          open={isModal}
          onClose={() => setIsModal(false)}
          handleSubmit={handleSubmit}
          selectedMembers={selectedMembers}
          setSelectedMembers={setSelectedMembers}
        />
      )}
      <FireDialog open={isFireDialog} handleClose={() => setIsFireDialog(false)} handleFire={handleSubmit} />
    </Card>
  )
}

export default MemberList
